.profile_page{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.container button{
    background: -webkit-linear-gradient(rgb(249 204 96), rgb(245, 57, 0));
    color: black;
    padding: 10px 25px;
    text-transform: uppercase;
    letter-spacing: 1.4px ;
    margin: 10px 0;
    font-size: 2rem;
    text-align: right;
    margin: 5rem 2rem;
}

.mydiv{
    margin-left: 70%
}

.profile_page .col-left{
    max-width: 400px;
    min-width: 300px;
    pad: 0 10px;
    margin: auto;
    
}

.profile_page .col-left .avatar{
    width: 150px;
    height: 150px;
    overflow: hidden;
    position: relative;
    margin: 15px auto;
    border:  1px solid #ddd;
    border-radius: 50%;
    cursor: pointer;
}

.profile_page .col-left img{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;

}

.profile_page .col-left .avatar span{
   position: absolute;
   bottom: -100%;
   left: 0;
   width: 100%;
   height: 80%;
   background: #fff8;
   text-align: center;
   font-weight: 400;
   text-transform: uppercase;
   color: rgb(255,140,45);
   transition: 0.3s ease-in-out;
}

.profile_page .col-left .avatar:hover span{
    bottom: -5%;
}

.profile_page .col-left #file-up{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;

}

.profile_page .col-left h2{
    text-align: center;
    font-size: 2.8rem;
    text-transform: uppercase;
    color: #333;
    margin: 10px 0;
}

.profile_page .col-left label{
    display: block;
    margin-top: 5px;
    font-size: 2rem;

}

.profile_page .col-left input{
    width: 100%;
    height: 40px;
    margin: 5px 0;
    border: none;
    outline: none;
    background: #fee;
    padding:  0 5px;
    font-size: 1.8rem;
}

.profile_page .col-left button{
    background: #ff0000;
    color: white;
    padding: 10px 25px;
    text-transform: uppercase;
    letter-spacing: 1.4px ;
    margin: 10px 0;
    font-size: 2rem;

}

.profile_page .col-right{
    padding: 0 10px;
    flex: 1;
    min-width: 300px;
    margin-right: 90px;
    margin-left: 50px;
    
}

.profile_page .col-right h2{
    font-size: 1.7rem;
    text-transform: uppercase;
    color: #333;
    margin: 10px 0;
}

.customers{
    border-collapse: collapse;
    width: 100%;
}

.customers td,
.customers th{
    border: 1px solid #ddd;
    padding: 8px;
    min-width: 100px;
}

.customers tr:nth-child(even){
    background: #f2f2f2;
}

.customers th{
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background: #555;
    color: white;
}

.customers i{
    cursor: pointer;
}
.customers .fa-check{
    color: green;
}
.customers .fa-times{
    color: red;
}
.customers .fa-edit{
    background: whitesmoke;
    padding: 7px;
    color: #555;
    font-size: 10px;
}
.customers .fa-trash-alt{
    background: red;
    padding: 7px;
    color: white;
    font-size: 10px;
    margin-left: 7px;
}




.edit_user .go_back{
    background: white;
    cursor: pointer;
    margin: 30px;
    font-size: 1.2rem;
}
.edit_user .col-left{
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
}
.edit_user .col-left input{
    background: #fee;
}
.edit_user .col-left #isAdmin{
    width: 20px;
    height: 20px;
    transform: translateY(3px);
    margin: 10px 0;
}
.edit_user .col-left #isAdmin + label{
    display: inline-block;
    margin-left: 5px;
}